#contact {
    padding: 40px;
    background-color: #f4f4f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 20px auto;
    width: 80%;
    max-width: 800px;
  }
  
  #contact h2 {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 20px;
  }
  
  #contact p {
    font-size: 1.1rem;
    text-align: center;
    margin: 10px 0;
  }
  
  #contact a {
    color: #0073e6;
    text-decoration: none;
  }
  
  #contact a:hover {
    text-decoration: underline;
  }
  
  #contact .social-links {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }
  
  #contact .social-links a {
    font-size: 1.2rem;
    color: #333;
  }
  
  #contact .social-links a:hover {
    color: #0073e6;
  }
  
  footer {
    text-align: center;
    margin-top: 40px;
    font-size: 0.9rem;
    color: #777;
  }
  